<template>

  <div class="All">

<!--    时间栏-->
    <div class="TimeBox">
      <div class="Time">
        <div>{{truncatedTime(timeData,0,11)}}</div>
        <div>{{truncatedTime(timeData,12,17)}}</div>
      </div>
      <div class="Title">童年印象</div>
    </div>

<!--    内容栏-->
    <div class="MiddleBox" ref="scrollArea">
      <div class="DataBox"
           v-for="(item, index) in dataList.infoListItem"
           :key="index"
           style="overflow-y: scroll;">

        <div class="DataItemBox"
             :class="{ 'align-right': item.id === 1, 'align-left': item.id === 2 }"
        >
          <div :class="{ 'DataItem1': item.id === 1, 'DataItem2': item.id === 2 }"
          >{{item.data}}</div>
        </div>

      </div>
    </div>

<!--    操作栏-->
    <div class="bottomBox">

      <div class="bottomBox_Item continuouTalk"
           @touchstart.stop="pressButton"
           @touchend.stop="releaseButton"
           ontouchstart="return false;"
           onselectstart="return false;"
           :class="{ 'small': isPressed }">
        接着聊
        <i class="iconfont icon-chat" style="color: #7B8F43"></i>
      </div>

      <div class="bottomBox_Item continuouStory"
           @touchstart.stop="pressStoryButton"
           @touchend.stop="releaseStoryButton"
           ontouchstart="return false;"
           onselectstart="return false;"
           :class="{ 'small': isStoryPressed }">
        生成故事
        <i class="iconfont icon-jiantou" style="color:#1C4511;"></i>
      </div>

    </div>

  </div>

</template>

<script>
export default {
  name: "DataPage",
  data() {
    return {
      isPressed: false,
      isStoryPressed: false,
      dataList: {},
      timeData: ""
    }
  },
  mounted() {
    this.dataList = this.$route.query.data
    console.log(this.dataList)
    this.$data.timeData = this.$route.query.data.time
  },
  methods: {
    pressButton() {
      this.isPressed = true;
    },
// 松开键盘按钮
    releaseButton() {
      this.isPressed = false;
      this.$router.push({
        path: "/home",
        query: {
          id: this.dataList._id
        }
      })
      console.log(this.dataList._id)
    },

    // 松开键盘按钮
    releaseStoryButton() {
      this.isStoryPressed = false;
    },

    pressStoryButton() {
      this.isStoryPressed = true;
    },


    truncatedTime(time,n,x) {
      return time.substring(n, x);
    },
  }
}
</script>

<style scoped>
.MiddleBox {
  width: 100%;
  overflow: auto;
  margin: 0 auto;
  padding-top: 60px;
  box-sizing: border-box;
  padding-bottom: 100px;
}
.DataBox {
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.DataItemBox {
  width: 100%;
}
.DataItem1 {
  max-width: 70%;
  padding: 10px 15px;
  border-radius: 25px;
  margin: 10px 0;
  background-color: #CAD5AC;
  word-wrap: break-word;
  display: inline-block;
  text-align: left;
}
.DataItem2 {
  max-width: 70%;
  padding: 10px 15px;
  border-radius: 25px;
  margin: 10px 0;
  word-wrap: break-word;
  display: inline-block;
  background-color: #E1E5DF;
  text-align: left;
}

.align-right {
  text-align: right;
}

.align-left {
  text-align: left;
}

/*顶部时间栏*/
.TimeBox {
  position: fixed;
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  padding: 10px;
  z-index: 999;
  background-color: rgba(252,254,228, .3);
  backdrop-filter: blur(10px);
}
.Time {
  box-sizing: border-box;
  color: #a2a2a2;
  font-size: 17px;
}
.Title {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 19px;
  color: #839B84;
}

/*底部栏*/
.bottomBox {
  position: fixed;
  width: 90%;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  height: 55px;
  display: flex;
}
.bottomBox_Item {
  width: 45%;
  height: 100%;
  margin-left: 3.33%;
  background-color: #fff;
  border-radius: 20px;
  text-align: center;
  font-weight: 700;
  line-height: 55px;
  font-size: 18px;
  -webkit-user-select: none; /* Safari 3.1+ */
  -moz-user-select: none; /* Firefox 2+ */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Standard syntax */
  box-shadow: 0 0 10px 2px rgba(0,0,0, .2);
  transition: transform 0.2s;
}
.continuouTalk {
  background-color: #BDCC98;
}
.continuouStory {
  background-color: #80A281;
}
.small {
  transform: scale(0.9) !important;
}

</style>