<template>
  <div class="All">

<!--    所有信息栏-->
    <div class="MiddleBox" ref="scrollArea">
      <div class="DataBox"
           v-for="(item, index) in infoList"
           :key="index"
           style="overflow-y: scroll;">

        <div class="DataItemBox"
             :class="{ 'align-right': item.id === 1, 'align-left': item.id === 2 }"
        >
          <div :class="{ 'DataItem1': item.id === 1, 'DataItem2': item.id === 2 }"
          >{{item.data}}</div>
        </div>

      </div>
    </div>

<!--    <div style="height: 1000px; background-color:#797979;"></div>-->

    <!--    按住说话按钮-->
    <div class="speakBigBox">

<!--      文本框-->
      <textarea class="speakText"
                v-if="istext"
                v-model="text">

      </textarea>

      <div class="speakBox_Two">
        <div class="jianPan"
             :style="istext ? { backgroundColor: '#4fabe5' } : { backgroundColor: 'gray' }"
             @touchstart.stop="pressJpButton"
             @touchend.stop="releaseJpButton"
             :class="{ 'small': isJpPressed }"
             ontouchstart="return false;"
             onselectstart="return false;">
          <i class="iconfont icon-jianpan1"></i>
        </div>
        <div class="SpeakBox"
             @touchstart.stop="pressButton"
             @touchend.stop="releaseButton"
             :class="{ 'small': isPressed }"
             ontouchstart="return false;"
             onselectstart="return false;"
        >
          <i class="ht iconfont icon-huatong"></i>
          按住说话
        </div>
        <div class="faSong"
             :style="text ? { backgroundColor: '#4fabe5' } : { backgroundColor: 'gray' }"
             @touchstart.stop="pressFsButton"
             @touchend.stop="releaseFsButton"
             :class="{ 'small': isFsPressed }"
             ontouchstart="return false;"
             onselectstart="return false;">
          <i class="iconfont icon-fasong1"></i>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";

export default {
  name: 'HomeView',
  data() {
    return {
      isPressed: false,
      isJpPressed: false,
      isFsPressed: false,
      istext: false,
      text: "",
      recognition: null,
      infoList: [],
      infoLists: [],
      id: 0,
      infoNum: 0
    }
  },
  mounted() {
    this.getCachedData();
    this.scrollToBottom();
    this.id = this.$route.query.id

    // 如果是编辑部传来的，有id，则获取该id对应的信息放到infoList中
    if (this.id) {
      this.getIdInfo();
    }
  },

  methods: {
    getIdInfo() {
      const parsedId = parseInt(this.id);
      this.infoList = this.infoLists.find(item => item._id === parsedId).infoListItem;
      this.infoNum = this.infoList.length
    },

    // 获取当前日期
    getCurrentDateTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      const hours = String(now.getHours()).padStart(2, '0');
      const minutes = String(now.getMinutes()).padStart(2, '0');
      return `${year}年${month}月${day}日 ${hours}:${minutes}`;
    },

    generateUniqueId() {
      return Date.now() + Math.floor(Math.random() * 1000);
    },
    saveInfoToCache() {
      if (this.infoList.length !== 0) {
        const dateTime = this.getCurrentDateTime();
        const uniqueId = this.generateUniqueId();
        const info = {
          time: dateTime,
          _id: uniqueId,
          infoListItem: [...this.infoList]
        };
        this.infoLists.push(info);
        console.log(this.infoLists)
        localStorage.setItem('infoLists', JSON.stringify(this.infoLists));
      }
    },
    // 将页面下拉至最底端
    scrollToBottom() {
      Vue.nextTick(() => {
        this.$refs.scrollArea.scrollTop = this.$refs.scrollArea.scrollHeight;
      });
    },

    getCachedData() {
      // this.$localStorage.clear();
      // localStorage.removeItem('infoLists');

      // 从 localStorage 获取数据
      const cachedInfoList = localStorage.getItem('infoLists');

      // 检查是否存在缓存数据
      if (cachedInfoList) {
        this.infoLists = JSON.parse(cachedInfoList);
      } else {
        console.log('没有找到缓存数据');
      }
    },

    // 按下语音按钮
    pressButton() {
      if (!this.istext) {
        this.istext = true
      }
      this.isPressed = true;
      // this.istext = true;
      this.recognition = new window.webkitSpeechRecognition(); // 使用 WebkitSpeechRecognition，在某些浏览器中可能需要前缀
      this.recognition.lang = 'zh-CN'; // 设置识别语言为中文
      this.recognition.continuous = true; // 开启连续识别
      this.recognition.onresult = (event) => {
        this.$data.text = event.results[event.results.length - 1][0].transcript;
        for (let i = 0; i < event.results.length; i++) {
          console.log(event.results[i][0].transcript);
        }
      };
      this.recognition.start();
    },

    // 松开语音按钮
    releaseButton() {
      if (this.recognition) {
        this.recognition.stop();
        this.recognition = null;
      }
      // if (this.text === "") {
      //   this.istext = false;
      // }
      this.isPressed = false;
    },

    // 按下键盘按钮
    pressJpButton() {
      this.isJpPressed = true;
    },

    // 松开键盘按钮
    releaseJpButton() {
      // this.istext = true;
      this.isJpPressed = false;
      this.istext = !this.istext;
    },

    // 按下发送按钮
    pressFsButton() {
      if (this.text) {
        this.isFsPressed = true;
      }
    },

    // 松开发送按钮
    releaseFsButton() {
      if (this.text) {
        this.isFsPressed = false;
        this.handleTranslate();

        const id = parseInt(this.id)
        const index = this.infoLists.findIndex(item => item._id === id);
        if (index !== -1) {
          this.infoLists.splice(index, 1);
        }

      }
    },

    handleTranslate() {

      const dataitem = this.$data.text;
      const newItem = {
        id: 1,
        data: dataitem
      };
      this.$data.text = "";
      this.istext = false;
      this.scrollToBottom()
      this.infoList.push(newItem)

      axios.post(
          "https://aip.baidubce.com/rpc/2.0/mt/texttrans/v1?access_token=24.8f9127728ff2e7ac7ff43a33eb40db88.2592000.1720589798.282335-80733545",
          {
            // 这里放请求主体的数据
            // 例如：
            q: dataitem,
            from: "zh",
            to: "en"
          },
          {
            headers: {
              "Content-Type": "application/json;charset=utf-8"
            }
          }
      )
          .then(res => {
            const newItem = {
              id: 2,
              data: res.data.result.trans_result[0].dst
            };
            console.log(res)
            this.infoList.push(newItem)
            this.scrollToBottom()

          })
          .catch(error => {
            console.error(error);
          });

    },

  },
  beforeDestroy() {
    if (this.infoList.length !== this.infoNum) {
      this.saveInfoToCache();
    }
  }
}
</script>

