import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import IndexPage from "@/views/IndexPage.vue";
import DataPage from "@/views/DataPage.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    meta: {
      title: '文本翻译'
    }
  },
  {
    path: '/',
    name: 'indexpage',
    component: IndexPage,
    meta: {
      title: '时光之语'
    }
  },
  {
    path: '/datapage',
    name: 'datapage',
    component: DataPage,
    meta: {
      title: '时光之语'
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
